@font-face {
  font-style: normal;
  font-weight: normal;
  src: url('../../sketches/assets/supratetris/DarumadropOne-Regular.ttf');
  font-family: 'Darumadrop One';
}

.main {
  color: #ddca7d;
  font-family: 'Darumadrop One', sans-serif;
}

#respin-button {
  display: none;
  position: absolute;
  transform: translate(8vw, 8vh);
  z-index: 2;
  cursor: pointer;
  margin: auto;
  border: 3px solid #ddca7d;
  border-radius: 50px;
  background-color: #1b1924;
  padding: 5px 0 45px;
  width: 200px;
  height: 50px;
  color: #ddca7d;
  font-size: 1.2rem;
  font-family: 'Darumadrop One', sans-serif;

  &:hover {
    background-color: #ddca7d;
    color: #242331;
  }
}

#Supratetris {
  display: flex;
  width: 100%;
  height: 100%;
  > canvas {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    margin: auto;
  }
}
